//1
/* –°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –∞–≤—Ç–æ–º–æ–±–∏–ª—å (–ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å, –º–æ–¥–µ–ª—å, –≥–æ–¥ –≤—ã–ø—É—Å–∫–∞, —Å—Ä–µ–¥–Ω—è—è —Å–∫–æ—Ä–æ—Å—Ç—å), 
–∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º:

–§—É–Ω–∫—Ü–∏—è –¥–ª—è –≤—ã–≤–æ–¥–∞ –Ω–∞ —ç–∫—Ä–∞–Ω –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –∞–≤—Ç–æ–º–æ–±–∏–ª–µ;

–§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–¥—Å—á–µ—Ç–∞ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ–≥–æ –≤—Ä–µ–º–µ–Ω–∏ –¥–ª—è –ø—Ä–µ–æ–¥–æ–ª–µ–Ω–∏—è –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏—è —Å–æ —Å—Ä–µ–¥–Ω–µ–π —Å–∫–æ—Ä–æ—Å—Ç—å—é.
–£—á—Ç–∏—Ç–µ, —á—Ç–æ —á–µ—Ä–µ–∑ –∫–∞–∂–¥—ã–µ 4 —á–∞—Å–∞ –¥–æ—Ä–æ–≥–∏ –≤–æ–¥–∏—Ç–µ–ª—é –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–µ–ª–∞—Ç—å –ø–µ—Ä–µ—Ä—ã–≤ –Ω–∞ 1 —á–∞—Å.*/


const hwFiveTaskOne = document.getElementById("hw5.1");
hwFiveTaskOne.addEventListener("click", function() {

const car = {
    munufacturer:"Japan",
    model: "Subaru Forester",
    year : 2010,
    "speed average": 40
}

const showInfoCar = () => {

    for(let key in car) {
            console.log(`${key} : ${car[key]}`);
        }
}
  
    console.log(`–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–± –∞–≤—Ç–æ–º–æ–±–∏–ª–µ:`);
    showInfoCar();
    alert(`–†–µ–∑—É–ª—å—Ç–∞—Ç –≤ –∫–æ–Ω—Å–æ–ª–µ!`)

const distance = Number(prompt("–í–≤–µ–¥–∏—Ç–µ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ: ","800"));

    const calcTime = (distance, speed) => {

        let time = distance / speed;
        if(time > 4) {
            time = time + Math.floor(time/4);
            return time;
        }

        return time;
    }

    const resTime = calcTime(distance, car[ "speed average"]);
    console.log(`–í—Ä–µ–º—è –¥–ª—è –ø—Ä–µ–æ–¥–æ–ª–µ–Ω–∏—è ${distance}–∫–º —Å–æ —Å—Ä–µ–¥–Ω–µ–π —Å–∫–æ—Ä–æ—Å—Ç—å—é ${car[ "speed average"]} –∫–º/—á:
    ${resTime} —á–∞—Å–æ–≤`);


});

