/*–°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –≤—Ä–µ–º—è (—á–∞—Å—ã, –º–∏–Ω—É—Ç—ã, —Å–µ–∫—É–Ω–¥—ã), –∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º: 
–§—É–Ω–∫—Ü–∏—è –≤—ã–≤–æ–¥–∞ –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —ç–∫—Ä–∞–Ω;

–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–µ–∫—É–Ω–¥;

–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω—É—Ç;

–§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —á–∞—Å–æ–≤. 

–£—á—Ç–∏—Ç–µ, —á—Ç–æ –≤ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö 3-—Ö —Ñ—É–Ω–∫—Ü–∏—è—Ö, –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –æ–¥–Ω–æ–π —á–∞—Å—Ç–∏ –≤—Ä–µ–º–µ–Ω–∏, –º–æ–∂–µ—Ç –∏–∑–º–µ–Ω–∏—Ç—å—Å—è –∏ –¥—Ä—É–≥–∞—è.
 –ù–∞–ø—Ä–∏–º–µ—Ä, –µ—Å–ª–∏ –∫–æ –≤—Ä–µ–º–µ–Ω–∏ ¬´20:30:45¬ª –¥–æ–±–∞–≤–∏—Ç—å 30 —Å–µ–∫—É–Ω–¥, —Ç–æ –¥–æ–ª–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å—Å—è ¬´20:31:15¬ª, –∞ –Ω–µ ¬´20:30:75¬ª.
*/

const hwFiveTaskThree = document.getElementById("hw5.3");
hwFiveTaskThree.addEventListener("click", function() {

    function Time(hours, minutes, seconds){
        return {hours:hours, minutes:minutes, seconds:seconds};
    }

    const firstNumber = Time(20,30,45); //obj

    const showTime = (obj) => {

        const hoursStr = obj.hours.toString();
        const minutesStr = obj.minutes.toString();
        const secondsStr = obj.seconds.toString();

        const strtime = `${hoursStr},${minutesStr},${secondsStr}`;
        const arrTime = strtime.split(',');
            
        for(let i = 0; i < arrTime.length; i++ ) {
            if( arrTime[i] < 10 ){
                arrTime[i] = "0" + arrTime[i];
                }
            }

        return `${arrTime[0]} : ${arrTime[1]} : ${arrTime[2]}`;
    }

    const addSecondsInTime = (obj, sec) => {

        let seconds = obj.seconds + sec;
        let minutes = obj.minutes;
        let hours = obj.hours;

        if(seconds > 60){
            seconds = seconds - 60;
            minutes += 1; 
            if(minutes >= 60) {
                minutes = minutes - 60;
                hours += 1;
            }
        }

        const newObj = Time(hours,minutes,seconds);
        return showTime(newObj);

    }

    const addMinutesInTime = (obj, min) => {

        let seconds = obj.seconds;
        let minutes = obj.minutes + min;
        let hours = obj.hours;

            if(minutes >= 60) {
                minutes = minutes - 60;
                hours += 1;
            }

        const newObj = Time(hours,minutes,seconds);
        return showTime(newObj);

    }

    const addhoursInTime = (obj, h) => {

        let seconds = obj.seconds;
        let minutes = obj.minutes;
        let hours = obj.hours + h;

            if(hours >= 24) {
                hours = hours - 24;
            }

        const newObj = Time(hours,minutes,seconds);
        return showTime(newObj);

    }

    console.log(`–û—Å–Ω–æ–≤–Ω–æ–µ –≤—Ä–µ–º—è`);
    console.log(showTime(firstNumber));
    console.log(`–ö –æ—Å–Ω–æ–≤–Ω–æ–º—É –≤—Ä–µ–º–µ–Ω–∏ + 30 —Å–µ–∫`);
    console.log(addSecondsInTime(firstNumber, 30));
    console.log(`–ö –æ—Å–Ω–æ–≤–Ω–æ–º—É –≤—Ä–µ–º–µ–Ω–∏ + 70 –º–∏–Ω`);
    console.log(addMinutesInTime(firstNumber, 70));
    console.log(`–ö –æ—Å–Ω–æ–≤–Ω–æ–º—É –≤—Ä–µ–º–µ–Ω–∏ + 2 —á–∞—Å–∞`);
    console.log(addhoursInTime(firstNumber, 2));

    alert(`—Ä–µ–∑—É–ª—å—Ç–∞—Ç –≤ –∫–æ–Ω—Å–æ–ª–∏!`);

});